<!-- 所有榜单 -->
<template>
<div class='AllRankList'>
  <div class="top-bar">
    <div class="back" @click="back">
     <van-icon name="arrow-left" size="0.6rem"/>
    </div>
    <van-tabs class="tab" v-model="active" animated line-width="15px">
    <van-tab class="item" :title="rank.name" v-for="(rank,index) in rankData" :key="index"
    @click="getTypeList()"  
    >
    </van-tab>
   </van-tabs>
  </div>
  <!--  -->
  <div class="content">
      <div class="left" v-if="rankData[active].subNavis">
     <van-sidebar v-model="activeKey">
        <van-sidebar-item :title="subNavi.name" v-for="(subNavi,index) in rankData[active].subNavis" 
        :key="index"
        @click="getBookList(subNavi)"
        />
    </van-sidebar>
    </div>
    <div class="right">
        <div class="book-box" v-for="(book,index) in booklistData.list" :key="index" 
        @click="getBookDetail(book.id,book.title)"
        >
          <div class="book-img">
             <img :src="book.cover" alt=""> 
          </div>
          <div class="book-content">
            <div class="title">
                {{book.title}}
            </div>
            <div class="content">
                {{book.content}}
            </div>
            <div class="author">
                {{book.author}} | {{book.category}}
            </div>
          </div>
        </div>
    </div>
  </div>
</div>
</template>

<script>

export default {
props:{},
data() {
//这里存放数据
return {
   url:'',
   i:0,
   active:0,
   activeKey:0,
   rankData:[],
   typeIndex:0,
   booklistData:[],
   rankUrl:'',
   rankType:0,
   uuid:'',
   all:0,
};
},
//监听属性 类似于data概念
computed: {
   
},
//监控data中的数据变化
watch: {
  
},
//方法集合
methods: {
  back(){
     this.$router.go(-1);
   },
   defaultRank(){
    if (this.i===0) {
      return  this.activeKey=1;
     }else if(this.i===2){
      return this.activeKey=4;
     }   
   },
   defaultRankList(){
     if(this.activeKey===1){
        this.axios.get("/store/rankList.json?rankType=26&uuid=ca03da7b64ca443ab25ec3b662fa91e2&all="+this.all).then(re=>{
          this.booklistData=re;
        })
     }else if(this.activeKey===0){
       this.axios.get("/store/rankList.json?rankType=34&uuid=ca03da7b64ca443ab25ec3b662fa91e2&all="+this.all).then(re=>{
          this.booklistData=re;
        })
     }else if(this.activeKey===4){
        this.axios.get("/store/rankList.json?rankType=806&uuid=ca03da7b64ca443ab25ec3b662fa91e2&all="+this.all).then(re=>{
          this.booklistData=re;
        })
     }
   },
   async getRankType(){
       await this.axios.get(`${this.url}`).then(re=>{
          this.rankData=re.list;
          console.log('this.rankData',this.rankData);
       })
   },
    getBookList(e){
    console.log('e',e);
    let a=e.naviInfos[0].url.split("&");
   this.rankType=a[0].split("=")[1];
   this.uuid=a[1].split("=")[1];
   this.all=a[2].split("=")[1];
    this.getBookListData();
   },
   getTypeList(){
    
   },
   async getBookListData(){
    await this.axios.get("/store/rankList.json?rankType=" + this.rankType + "&uuid=" + this.uuid + "&all=" + this.all).then(re=>{
       this.booklistData=re;
       console.log("this.booklistData",this.booklistData);
    })
   }, 
   getBookDetail(id, title) {
      this.$router.push({ name: "BookDetails", params: { id, title } });
    },
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
   this.url=this.$route.params.url;
//    console.log(this.url);
   this.i=this.$route.params.i;
//    console.log(this.i); 
  this.getRankType();
  this.defaultRank();
  this.getBookListData();
  this.defaultRankList();
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {
   
},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
components: {},
}
</script>
<style lang='scss' scoped>
.top-bar {
    display: flex;
    border-bottom: 1px solid grey;
     background-color: #ffffff;
     position: fixed;
    top: 0;
    left: 0;
  .back {
     margin: 0.3rem 0.8rem 0.2rem 0.5rem;
     background-color: #ffffff;
     z-index: 99;
  }
  .tab{
      
    .item{
        padding-right: 2rem;
    }
  }
}
.content {
    display: flex;
    width: 100vw;
  .left {
     background-color: #ffffff;
     position: fixed;
     left: 0;
     top: 7vh;
  }

  .right {
      margin-left: 24vw;
      margin-top: 5.6vh;
  .book-box {
      display: flex;
      justify-content: space-between;
      margin:3vh 0;
    .book-img {
        flex: 1;
        width: 8vw;
        height: 12vh;
      img {
       height: 100%;
       width: 100%;
       display: block;
      }
    }

    .book-content {
        flex: 4;
        display: flex;
        margin-left: 2vw;
        flex-direction: column;
        justify-content: space-around;
         .title {
           font-size: 14px;
           font-weight: bold;
        }

        .content {
        width: 100%;
       overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1; /* 限制在一个块元素显示的文本的行数 */
      -webkit-box-orient: vertical; /* 垂直排列 */
      word-break: break-all;  /* 内容自动换行 */
        }

        .author {

        }
    }
  }
}
}

</style>